@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap);
:root {
  --primary: #12232e;
  --secondary: #074e77;
  --third: #4da8da;
  --w-text: #eefbfb;
  --mobile-p: 1.1rem;
  --pad-p: 1.9rem;
  --desktop-p: 1.8rem;
  --mobile-title: 2.2rem;
  --pad-title: 3.3rem;
  --desktop-title: 3.3rem;
}

body {
  background-color: rgba(245, 244, 244, 0.959);
  font-family: "Playfair Display", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.top-container {
  margin: 30px 0px;
  width: 100vw;
  text-align: center;
}

.btn-top {
  transform: scale(2);
  color: #074e77;
  color: var(--secondary);
  background-color: #12232e;
  background-color: var(--primary);
  border-radius: 50%;
  box-shadow: -1px 2px 3px -1px #000000;
}

.btn-top:hover {
  cursor: pointer;
  -webkit-animation: 1s ease-in-out top infinite;
          animation: 1s ease-in-out top infinite;
}

@-webkit-keyframes top {
  0% {
    transform: translateY(0px) scale(2);
  }

  50% {
    transform: translateY(-5px) scale(2.2);
  }

  100% {
    transform: translateY(0px) scale(2);
  }
}

@keyframes top {
  0% {
    transform: translateY(0px) scale(2);
  }

  50% {
    transform: translateY(-5px) scale(2.2);
  }

  100% {
    transform: translateY(0px) scale(2);
  }
}

.home-text-container {
  height: 30%;
  width: 85%;
  margin-top: 120px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-shadow: -1px 1px 1px var(--primary);
  color: var(--secondary);
  z-index: 6;
}

.home-text-container h1 {
  margin: 0;
  font-size: 2.5rem;
}

.home-text-container p {
  margin: 0;
  font-size: 1.5rem;
}

.icons {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 20%;
  width: 100%;
}

.icons-animation {
  text-decoration: none;
  color: var(--secondary);
  text-shadow: -1px 1px 1px var(--primary);
  -webkit-animation: 1.3s ease-in-out flip;
          animation: 1.3s ease-in-out flip;
}

@-webkit-keyframes flip {
  from {
    transform: translateX(350px) rotate(360deg) scale(1.2);
    opacity: 0;
  }
}

@keyframes flip {
  from {
    transform: translateX(350px) rotate(360deg) scale(1.2);
    opacity: 0;
  }
}

@media screen and (min-width: 760px) {
  .home-text-container {
    width: 75%;
    height: 30%;
    margin-top: 200px;
  }

  .home-text-container h1 {
    margin: 0;
    font-size: 3.5rem;
  }

  .home-text-container p {
    font-size: 2rem;
  }

  .icons {
    height: 20%;
    width: 40%;
  }
}

@media screen and (min-width: 992px) {
  .home-text-container {
    position: absolute;
    left: 0px;
    top: 120px;
    width: 50%;
    height: 40%;
    margin-top: 0px;
  }

  .home-text-container h1 {
    margin: 0;
    font-size: 4.7rem;
  }

  .home-text-container p {
    font-size: 2.5rem;
  }

  .icons {
    height: 20%;
    width: 40%;
  }
}

.home-body {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  overflow: hidden;
}

.bg {
  height: 40%;
  width: 60%;
  position: absolute;
  bottom: 28px;
  z-index: 1000;
}

.bg-wave {
  width: 450%;
  position: absolute;
  bottom: 0;
  right: 0;
}

@media screen and (min-width: 760px) {
  .bg {
    height: 45%;
    width: 60%;
  }

  .bg-wave {
    width: 350%;
  }
}

@media screen and (min-width: 992px) {
  .bg {
    height: 85%;
    width: 45%;
    right: 50px;
  }

  .bg-wave {
    width: 150%;
    left: 0;
  }
}

.about-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  color: var(--secondary);
  font-weight: 500;
  overflow: hidden;
  position: relative;
}

.about-wave {
  position: absolute;
  width: 200%;
  left: 0;
  top: 0;
}

.about-me-svg {
  position: absolute;
  height: 30%;
  width: 28%;
  top: 0px;
  left: 35px;
  z-index: 1000;
}

.title {
  margin-top: 115px;
  margin-left: 90px;
  margin-bottom: 0px;
  font-size: var(--mobile-title);
}

.title span {
  color: var(--third);
}

.about-container p {
  margin-top: 30px;
  margin-bottom: 60px;
  width: 68%;
  font-size: var(--mobile-p);
  line-height: 1.8rem;
}

@media screen and (min-width: 760px) {
  .about-wave {
    width: 210%;
  }

  .about-me-svg {
    position: absolute;
    height: 40%;
    left: 90px;
  }

  .title {
    margin-top: 240px;
    margin-bottom: 20px;
    margin-left: 250px;
    text-align: center;
    font-size: var(--pad-title);
  }

  .about-container p {
    margin-top: 50px;
    margin-bottom: 150px;
    font-size: var(--pad-p);
    width: 75%;
    line-height: 2.7rem;
  }
}

@media screen and (min-width: 992px) {
  .about-container {
    align-items: flex-end;
  }

  .about-wave {
    width: 180%;
  }

  .about-me-svg {
    position: absolute;
    height: 80%;
    width: 30%;
    left: 90px;
  }

  .title {
    width: 60%;
    margin-top: 450px;
    margin-right: 80px;
    text-align: center;
    font-size: var(--desktop-title);
  }

  .about-container p {
    margin-top: 50px;
    margin-bottom: 300px;
    margin-right: 100px;
    font-size: var(--desktop-p);
    width: 55%;
    line-height: 3rem;
  }
}

.card {
  height: 18%;
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--primary);
  border-radius: 5%;
  overflow: hidden;
  box-shadow: -1px 2px 7px -1px #000000;
  -webkit-animation: 1s ease-in-out fade-in;
          animation: 1s ease-in-out fade-in;
}

.unseen {
  opacity: 0;
}

.project-image {
  height: 28%;
  width: 100%;
  border-top-left-radius: 5%;
  border-top-right-radius: 5%;
}

.card-content {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  color: var(--w-text);
  margin-top: 10px;
  height: 80%;
  width: 90%;
}

.project-links {
  margin-top: 15px;
  width: 100%;
  padding: 5px;
  background: var(--secondary);
  overflow: hidden;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.project-links a {
  padding: 0.5em 0;
  text-decoration: none;
  color: var(--w-text);
  font-size: 0.7rem;
}

.card-content h2 {
  color: var(--third);
  font-size: 1.6rem;
  font-weight: 600;
  margin: 0;
}

.card-content h4 {
  font-size: 1.1em;
  font-weight: 500;
  margin: 0;
}

.card-content ul {
  list-style-type: none;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  margin: 0;
  padding: 0;
}

.card-content li {
  font-size: 0.9em;
  font-weight: 300;
  margin: 10px 0;
}

.project-links a:hover {
  color: #d4d4d4;
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0.4;
    transform: translateY(500px) translateZ(500px) scale(0.6);
  }
}

@keyframes fade-in {
  from {
    opacity: 0.4;
    transform: translateY(500px) translateZ(500px) scale(0.6);
  }
}

@media screen and (min-width: 760px) {
  .card {
    height: 17%;
    width: 90%;
  }

  .project-image {
    height: 30%;
  }

  .card-content {
    height: 60%;
  }

  .project-links {
    margin-top: 15px;
  }

  .project-links a {
    padding: 1em 0;
    font-size: 1rem;
  }

  .card-content h2 {
    font-size: 2.6rem;
    font-weight: 600;
  }

  .card-content h4 {
    font-size: 1.85rem;
  }

  .card-content li {
    font-size: 1.6rem;
    font-weight: 100;
    margin: 10px 0;
  }
}

@media screen and (min-width: 992px) {
  .card {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    height: 15%;
    width: 70%;
    border-radius: 5%;
    box-shadow: -2px 1px 3px -1px #000000;
    position: relative;
    overflow: hidden;
  }

  .project-image {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 26%;
    border-top-left-radius: 0;
    border-top-right-radius: 2%;
    border-bottom-right-radius: 2%;
  }

  .card-content {
    align-items: flex-start;
    height: 80%;
    width: 80%;
  }

  .project-links {
    margin-top: 0px;
    height: 100%;
    width: 7%;
    overflow: hidden;
    flex-direction: column;
  }

  .project-links a {
    padding: 0 0.5rem;
    text-decoration: none;
    color: var(--w-text);
    font-size: 1rem;
  }

  .card-content h2 {
    font-size: 2.5rem;
  }

  .card-content h4 {
    font-size: 1.5rem;
  }

  .card-content ul {
    width: 60%;
  }

  .card-content li {
    font-weight: 100;
    margin: 10px 0;
    font-size: 1.05rem;
  }
}

