.portfolio-wrapper {
  height: 390vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.portfolio-wrapper h1 {
  margin-top: 100px;
  margin-bottom: 0;
  font-size: var(--mobile-title);
  color: var(--secondary);
}

.animation {
  -webkit-animation: 3s ease bounce;
          animation: 3s ease bounce;
}

@-webkit-keyframes bounce {
  0% {
    transform: scale(1, 1) translateY(0);
  }
  10% {
    transform: scale(1.1, 0.9) translateY(0);
  }
  30% {
    transform: scale(0.9, 1.1) translateY(-50px);
  }
  50% {
    transform: scale(1, 1) translateY(0);
  }
  100% {
    transform: scale(1, 1) translateY(0);
  }
}

@keyframes bounce {
  0% {
    transform: scale(1, 1) translateY(0);
  }
  10% {
    transform: scale(1.1, 0.9) translateY(0);
  }
  30% {
    transform: scale(0.9, 1.1) translateY(-50px);
  }
  50% {
    transform: scale(1, 1) translateY(0);
  }
  100% {
    transform: scale(1, 1) translateY(0);
  }
}
@media screen and (min-width: 760px) {
  .portfolio-wrapper {
    height: 420vh;
  }

  .portfolio-wrapper h1 {
    margin-top: 30px;
    margin-bottom: 0px;
    font-size: var(--pad-title);
  }
}

