.tech-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: var(--secondary);
}

.tech-grid {
  width: 80%;
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 80px;
  grid-gap: 15px;
  gap: 15px;
}

.tech-container h2 {
  width: 60%;
  margin-top: 100px;
  font-size: var(--mobile-title);
}

.tech-container p {
  width: 80%;
  font-size: var(--mobile-p);
}

@media screen and (min-width: 760px) {
  .tech-grid {
    grid-auto-rows: 100px;
    grid-gap: 20px;
    gap: 20px;
  }

  .tech-container h2 {
    font-size: var(--pad-title);
  }

  .tech-container p {
    width: 80%;
    font-size: var(--pad-p);
  }
}

@media screen and (min-width: 992px) {
  .tech-grid {
    width: 55%;
    grid-auto-rows: 100px;
    grid-gap: 20px;
    gap: 20px;
  }

  .tech-container h2 {
    font-size: var(--desktop-title);
  }

  .tech-container p {
    width: 80%;
    font-size: var(--desktop-p);
  }
}

.img-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  box-shadow: -1px 2px 3px -1px #00000021;
  border-radius: 10%;
  margin: 0 auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.tech-img {
  max-height: 50px;
}

.unseen {
  opacity: 0;
}

.tilt {
  -webkit-animation: 0.7s ease-in-out tilt;
          animation: 0.7s ease-in-out tilt;
}

@-webkit-keyframes tilt {
  from {
    opacity: 0;
    transform: rotate(30deg);
  }
  to {
    opacity: 1;
    transform: rotate(0deg);
  }
}

@keyframes tilt {
  from {
    opacity: 0;
    transform: rotate(30deg);
  }
  to {
    opacity: 1;
    transform: rotate(0deg);
  }
}

@media screen and (min-width: 760px) {
  .img-wrap {
    border-radius: 10%;
    overflow: hidden;
  }

  .tech-img {
    max-height: 90px;
  }
}

@media screen and (min-width: 992px) {
  .tech-img {
    max-height: 55px;
  }
}

.contact-me {
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.contact-me h2 {
  margin-top: 95px;
  margin-bottom: 2px;
  font-size: var(--mobile-title);
  color: var(--secondary);
}

.contact-icons {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  overflow: hidden;
}

.contact-icons i {
  color: var(--third);
  background-color: var(--w-text);
}

.contact-me p {
  margin: 15px 0px;
  font-size: var(--mobile-p);
}

.contact-form {
  background-color: var(--primary);
  height: 480px;
  width: 80%;
  padding: 20px 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  box-shadow: -1px 2px 7px -1px #000000;
  border-radius: 5%;
  overflow: hidden;
  margin: 0;
}

.contact-form input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 50px var(--primary) inset; /* Change the color to your own background color */
  -webkit-text-fill-color: var(--w-text);
}

.contact-form input:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0 50px var(--primary) inset; /*your box-shadow*/
  -webkit-text-fill-color: var(--w-text);
}

.contact-form input {
  margin-bottom: 20px;
  padding: 8px;
  border: none;
  border-bottom: 1px solid var(--secondary);
  width: 80%;
  font-size: 18px;
  color: var(--w-text);
  background: transparent;
}
.contact-form input:focus {
  outline: 1px solid var(--secondary);
}

.contact-form textarea {
  margin-top: 20px;
  padding: 8px;
  height: 50%;
  width: 80%;
  font-size: 18px;
  background: transparent;
  color: var(--w-text);
  border: none;
  border-bottom: 1px solid var(--secondary);
}

.contact-form textarea:focus {
  outline: 1px solid var(--secondary);
}

.contact-form textarea:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 50px var(--primary) inset; /* Change the color to your own background color */
  -webkit-text-fill-color: var(--w-text);
}

.contact-form textarea:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0 50px var(--primary) inset; /*your box-shadow*/
  -webkit-text-fill-color: var(--w-text);
}

.contact-form button {
  margin-top: 20px;
  padding: 10px 30px;
  background-color: var(--secondary);
  border: 1px solid var(--secondary);
  border-radius: 10%;
}

.contact-form button:hover {
  cursor: pointer;
  transform: scale(1.1);
  border: 1px solid var(--w-text);
}

.contact-form i {
  background-color: var(--secondary);
}

.err-text {
  margin: 0;
  color: var(--w-text);
  font-size: 14px;
}

@media screen and (min-width: 760px) {
  .contact-form {
    height: 700px;
    width: 70%;
  }
  .contact-me h2 {
    margin-top: 120px;
    margin-bottom: 0px;
    font-size: var(--pad-title);
  }

  .contact-icons {
    width: 50%;
  }

  .contact-icons i {
    background-color: var(--w-text);
  }

  .contact-me p {
    font-size: var(--pad-p);
  }

  .contact-form input {
    font-size: 22px;
  }

  .contact-form input:focus {
    outline: 1px solid var(--secondary);
  }

  .contact-form textarea {
    font-size: 22px;
  }

  .err-text {
    margin: 0;
    color: var(--w-text);
    font-size: 17px;
    font-weight: 300;
  }
}

@media screen and (min-width: 992px) {
  .contact-form {
    height: 400px;
    width: 30%;
  }

  .contact-me h2 {
    margin-top: 120px;
    margin-bottom: 0px;
    font-size: var(--desktop-title);
  }

  .contact-icons {
    width: 30%;
  }

  .contact-icons i {
    background-color: var(--w-text);
  }

  .contact-icons i:hover {
    color: var(--secondary);
    background-color: var(--w-text);
  }

  .contact-me p {
    font-size: var(--desktop-p);
  }
}

